import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { useGetTripsByCompanyQuery , useDeleteExpiredTripsForCompanyMutation  } from '../../../Redux/Trip/TripApiSlice';
import TripList from '../components/ManageTrip/TripList';
import TripForm from '../components/ManageTrip/TripForm';
import { Button, Drawer, Typography, Spin, Alert, notification ,Popconfirm } from 'antd';

const { Title } = Typography;

const ManageTrips = () => {
  const { companyId } = useSelector((state) => state.user.userInfo || {});
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const [editingTripId, setEditingTripId] = useState(null);
  const [formMode, setFormMode] = useState('add');

  const { data = {}, isLoading, error, refetch } = useGetTripsByCompanyQuery(companyId, {
    skip: !companyId,
  });

  const trips = data.trips || [];

  const [deleteExpiredTripsForCompany, { isLoading: isDeleting }] = useDeleteExpiredTripsForCompanyMutation();

  // Hm hin th thng bo
  const showNotification = (type, message, description) => {
    notification[type]({
      message,
      description,
    });
  };

  const openDrawer = (trip = null) => {
    if (trip) {
      setEditingTripId(trip._id);
      setFormMode('edit');
    } else {
      setEditingTripId(null);
      setFormMode('add');
    }
    setIsDrawerOpen(true);
  };

  const closeDrawer = (actionSuccess, message) => {
    setIsDrawerOpen(false);
    setEditingTripId(null);
    setFormMode('add');
    if (actionSuccess) {
      showNotification('success', 'Thnh cng', message);
    }
    refetch();
  };

  const handleDeleteExpiredTrips = async () => {
    try {
      const response = await deleteExpiredTripsForCompany();
      showNotification('success', 'Thnh cng', response.data.message || 'Cc chuyn i  qu hn  c xa thnh cng.');
      refetch();
    } catch (error) {
      const errorMessage = error.response?.data?.message || 'C li xy ra khi xa cc chuyn i  qu hn.';
      showNotification('error', 'Li', errorMessage);
    }
  };

  return (
    <div className="p-6 bg-white shadow-md rounded-lg max-w-screen-lg mx-auto">
      <div className="flex justify-between items-center mb-6">
        <Title level={3} className="text-gray-700">Qun L Chuyn i</Title>
        <div className="space-x-4">
          <Popconfirm
            title="Bn c chc chn mun xa tt c cc chuyn i  qu hn?"
            onConfirm={handleDeleteExpiredTrips}
            okText="Xa"
            cancelText="Hy"
          >
            <Button 
              type="primary" 
              danger 
              size="large" 
              loading={isDeleting} 
              style={{ backgroundColor: '#ff4d4f', borderColor: '#ff4d4f' }}
            >
              Xa Chuyn i Qu Hn
            </Button>
          </Popconfirm>
          {!isDrawerOpen && (
            <Button type="primary" size="large" onClick={() => openDrawer()}>
              To Chuyn i
            </Button>
          )}
        </div>
      </div>

      {isLoading ? (
        <div className="flex justify-center items-center py-10">
          <Spin tip="ang ti danh sch chuyn i..." size="large" />
        </div>
      ) : error ? (
        <div className="flex justify-center items-center py-10">
          <Alert message="C li xy ra khi ti danh sch chuyn i" description={error.message} type="error" showIcon />
        </div>
      ) : (
        <TripList 
          trips={trips} 
          openDrawer={openDrawer} 
          hideAddButton={isDrawerOpen} 
          showNotification={showNotification} 
          refetch={refetch} 
        />
      )}

      <Drawer
        title={editingTripId ? 'Chnh Sa Chuyn i' : 'Thm Chuyn i Mi'}
        width={500}
        onClose={() => closeDrawer(false)}
        visible={isDrawerOpen}
        destroyOnClose
      >
        <TripForm
          tripId={editingTripId}
          closeDrawer={(successMessage) => closeDrawer(true, successMessage)}
          trips={trips}
          formMode={formMode}
          showNotification={showNotification}
        />
      </Drawer>
    </div>
  );
};

export default ManageTrips;
